@keyframes main-animation {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(0.5);
  }
}

@keyframes height-s {
  from {
    height: 100%;
  }
  to {
    height: 100vh;
  }
}

@keyframes appear {
  from {
    opacity: 0;
    transform: translateX(20%);
  }
  to {
    opacity: 1;
    transform: translateX(0px);
  }
}

@keyframes hide {
  0% {
    opacity: 1;
    transform: translateX(0px);
  }
  80% {
    opacity: 0;
    transform: translateX(20%);
  }
  100% {
    opacity: 0;
    transform: translateX(500%);
  }
}

@keyframes scroll-lead {
  0% {
    transform: scale(1, 0);
    transform-origin: 0 0;
  }
  50% {
    transform: scale(1, 1);
    transform-origin: 0 0;
  }
  51% {
    transform: scale(1, 1);
    transform-origin: 0 100%;
  }
  100% {
    transform: scale(1, 0);
    transform-origin: 0 100%;
  }
}

@keyframes block-appear {
  from {
    opacity: 0;
    transform: translateY(15%);
  }
  to {
    opacity: 1;
    transform: translateY(0%);
  }
}

@keyframes noise-1 {
  0%, 20%, 40%, 60%, 70%, 90% {
    opacity: 0;
  }
  10% {
    opacity: .1;
  }
  50% {
    opacity: .5;
    left: -6px;
  }
  80% {
    opacity: .3;
  }
  100% {
    opacity: .6;
    left: 2px;
  }
}

@keyframes noise-2 {
  0%, 20%, 40%, 60%, 70%, 90% {
    opacity: 0;
  }
  10% {
    opacity: .1;
  }
  50% {
    opacity: .5;
    left: 6px;
  }
  80% {
    opacity: .3;
  }
  100% {
    opacity: .6;
    left: -2px;
  }
}

@keyframes noise {
  0%, 3%, 5%, 42%, 44%, 100% {
    opacity: 1;
    transform: scaleY(1);
  }
  4.3% {
    opacity: 1;
    transform: scaleY(1.7);
  }
  43% {
    opacity: 1;
    transform: scaleX(1.5);
  }
}

@keyframes noise-3 {
  0%, 3%, 5%, 42%, 44%, 100% {
    opacity: 1;
    transform: scaleY(1);
  }
  4.3% {
    opacity: 1;
    transform: scaleY(4);
  }
  43% {
    opacity: 1;
    transform: scaleX(10) rotate(60deg);
  }
}

@keyframes grain {
  0%, 100% {
    transform: translate(0, 0);
  }
  10% {
    transform: translate(-5%, -10%);
  }
  20% {
    transform: translate(-15%, 5%);
  }
  30% {
    transform: translate(7%, -25%);
  }
  40% {
    transform: translate(-5%, 25%);
  }
  50% {
    transform: translate(-15%, 10%);
  }
  60% {
    transform: translate(15%, 0%);
  }
  70% {
    transform: translate(0%, 15%);
  }
  80% {
    transform: translate(3%, 35%);
  }
  90% {
    transform: translate(-10%, 10%);
  }
}

@keyframes marquee {
  0% {
    transform: translate3d(var(--move-initial), 0, 0);
  }
  100% {
    transform: translate3d(var(--move-final), 0, 0);
  }
}

@keyframes PageAnime {
  0% {
    transform-origin: left;
    transform: scaleX(0);
  }
  50% {
    transform-origin: left;
    transform: scaleX(1);
  }
  50.001% {
    transform-origin: right;
  }
  100% {
    transform-origin: right;
    transform: scaleX(0);
  }
}

@keyframes PageAnimeAppear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

html {
  /*
    計算式：10px /  デザインカンプの横幅 * 100
    画面サイズがデザインカンプ幅の時font-size = 10pxになる
    */
  font-size: calc(10 / 1920 * 100)vw;
}

@media (max-width: 768px) {
  html {
    font-size: calc(10 / 768 * 100)vw;
  }
}

@media (max-width: 480px) {
  html {
    font-size: calc(10 / 375 * 100)vw;
  }
}

body {
  font-size: 1.6rem;
  font-family: "游ゴシック体", YuGothic, "游ゴシック", "Yu Gothic", Meiryo, sans-serif;
  position: relative;
  line-height: 1;
  background-image: url("../images/background.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.wrapper {
  width: 100%;
  overflow-x: hidden;
}

.container {
  width: 1168px;
  margin: 0 auto;
}

@media (min-width: 769px) and (max-width: 1170px) {
  .container {
    width: 84vw;
  }
}

@media (max-width: 768px) {
  .container {
    width: 84vw;
  }
}

@media (max-width: 768px) {
  .br-pc {
    display: none;
  }
}

.head-title {
  height: auto;
  position: relative;
  padding-left: 1.6rem;
}

@media (min-width: 1921px) {
  .head-title {
    padding-left: 16px;
  }
}

@media (max-width: 768px) {
  .head-title {
    padding-left: 1.2rem;
  }
}

.head-title:before {
  content: '';
  display: inline-block;
  width: 8px;
  height: 4rem;
  border-radius: 4px;
  background-color: #518C03;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

@media (min-width: 1921px) {
  .head-title:before {
    height: 40px;
  }
}

@media (max-width: 768px) {
  .head-title:before {
    width: 4px;
    border-radius: 2px;
  }
}

.link-btn {
  font-size: 1.6rem;
  color: #ffffff;
  font-weight: bold;
  display: inline-block;
  text-align: center;
  width: 26.8rem;
  height: 4.8rem;
  line-height: 4.8rem;
  border-radius: 2.4rem;
  background-color: #993A22;
}

.link-btn i {
  margin-left: 0.8rem;
}

.header {
  width: 100%;
}

.header__inner {
  padding: 1.6rem 0;
  display: flex;
  align-items: center;
}

@media (min-width: 1921px) {
  .header__inner {
    padding: 16px 0;
  }
}

@media (max-width: 768px) {
  .header__inner {
    padding: 1.2rem 0;
  }
}

.header__logo {
  width: 42.4rem;
}

@media (min-width: 1921px) {
  .header__logo {
    width: 424px;
  }
}

@media (max-width: 768px) {
  .header__logo {
    width: 24.8rem;
  }
}

.header__sns {
  margin-right: 3.2rem;
  margin-left: auto;
}

@media (min-width: 1921px) {
  .header__sns {
    margin-right: 32px;
  }
}

@media (max-width: 768px) {
  .header__sns {
    display: none;
  }
}

.header__sns-item {
  margin-right: 1.6rem;
}

@media (min-width: 1921px) {
  .header__sns-item {
    margin-right: 16px;
  }
}

.header__sns-item:last-child {
  margin-right: 0;
}

@media (max-width: 768px) {
  .header__search {
    display: none;
  }
}

.search {
  width: 26.8rem;
}

@media (min-width: 1921px) {
  .search {
    width: 268px;
  }
}

.search_size_l {
  width: 20rem;
}

@media (min-width: 1921px) {
  .search_size_l {
    width: 200px;
  }
}

.search__box {
  height: 3.8rem;
  border-radius: 1.9rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  padding: 0 1.6rem;
  border: solid 1px #333333;
  background-color: #ebebeb;
}

@media (min-width: 1921px) {
  .search__box {
    height: 30px;
    border-radius: 80px;
    padding: 0 15px;
  }
}

.search__input {
  font-size: 1.4rem;
  color: #333333;
  font-weight: 700;
  display: inline-block;
  line-height: 1;
  max-width: 100%;
}

@media (min-width: 1921px) {
  .search__input {
    font-size: 14px;
  }
}

.search__submit {
  display: inline-block;
  width: 1.6rem;
  height: 1.6rem;
}

.sns {
  display: flex;
}

.sns__item {
  display: inline-block;
  width: 3.2rem;
  height: 3.2rem;
}

@media (min-width: 1921px) {
  .sns__item {
    width: 32px;
    height: 32px;
  }
}

.global-navi {
  background-color: #518C03;
  width: 100%;
  height: 7.3rem;
  transition: transform 0.5s ease;
  z-index: 1000;
}

@media (min-width: 1921px) {
  .global-navi {
    height: 73px;
  }
}

@media (max-width: 768px) {
  .global-navi {
    position: fixed;
    top: 0;
    right: 0;
    height: 100vh;
    padding-top: 8rem;
    transform: translateX(100%);
    overflow-y: scroll;
  }
}

.global-navi.menu-open {
  transform: translateX(0%);
  transition: transform 0.5s ease;
}

.global-navi__sns {
  margin-right: 3.2rem;
}

@media (min-width: 1921px) {
  .global-navi__sns {
    margin-right: 32px;
  }
}

@media (max-width: 768px) {
  .global-navi__sns {
    margin: 4rem 0;
    text-align: center;
  }
}

.global-navi .global-navi__sns {
  display: none;
}

@media (max-width: 768px) {
  .global-navi .global-navi__sns {
    display: block;
  }
}

.global-navi__search {
  display: none;
}

@media (max-width: 768px) {
  .global-navi__search {
    display: block;
    margin: 0 auto;
  }
}

.global-navi__list {
  height: 100%;
  display: flex;
}

@media (max-width: 768px) {
  .global-navi__list {
    flex-direction: column;
  }
}

.global-navi__list--second {
  background-color: #518C03;
}

.global-navi__item {
  width: calc(100% / 7);
  height: 100%;
  position: relative;
  z-index: 10;
  background-color: #518C03;
  transition: filter 0.3s ease;
}

.global-navi__item:hover {
  transition: filter 0.3s ease;
  filter: brightness(120%);
}

.global-navi__item:after {
  content: '';
  display: block;
  width: 1px;
  height: 4.4rem;
  background-color: #ffffff;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}

@media (min-width: 1921px) {
  .global-navi__item:after {
    height: 44px;
  }
}

@media (max-width: 768px) {
  .global-navi__item:after {
    width: 100%;
    height: 1px;
    top: 0;
    left: 0;
    transform: translateY(0%);
  }
}

@media (max-width: 768px) {
  .global-navi__item {
    width: 100%;
    height: auto;
    padding: 1.3rem 0;
  }
}

.global-navi__item--second {
  background-color: #518C03;
}

@media (max-width: 768px) {
  .global-navi__item--second {
    margin-top: 1.2rem;
  }
}

.global-navi__link {
  font-size: 1.4rem;
  color: #ffffff;
  font-weight: bold;
  line-height: 1.5;
  height: 100%;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (min-width: 1921px) {
  .global-navi__link {
    font-size: 14px;
  }
}

@media (max-width: 768px) {
  .global-navi__link {
    font-size: 1.6rem;
    height: auto;
    justify-content: flex-start;
    line-height: 1;
  }
}

.global-navi__link i {
  padding-left: 1.6rem;
}

@media (max-width: 768px) {
  .global-navi__link i {
    display: none;
  }
}

.global-navi__link--second {
  font-size: 1.4rem;
  color: #ffffff;
  font-weight: bold;
  line-height: 4.4rem;
  text-align: center;
  display: block;
  height: 100%;
}

@media (min-width: 1921px) {
  .global-navi__link--second {
    font-size: 14px;
    line-height: 44px;
  }
}

@media (max-width: 768px) {
  .global-navi__link--second {
    text-align: left;
    line-height: 1.8rem;
    padding-left: 4rem;
  }
}

.global-navi__link--second i {
  display: none;
}

@media (max-width: 768px) {
  .global-navi__link--second i {
    display: inline-block;
    margin-right: 0.9rem;
  }
}

.global-navi__icon {
  display: none;
  width: 2.4rem;
  height: 2.4rem;
}

@media (max-width: 768px) {
  .global-navi__icon {
    display: inline-block;
    margin-right: 1.2rem;
  }
}

.menu-item-has-children ul li {
  border-top: none;
  height: 0;
  overflow: hidden;
  transition: height 0.3s ease;
}

@media (max-width: 768px) {
  .menu-item-has-children ul li {
    height: auto;
    overflow: visible;
  }
}

.menu-item-has-children:hover ul li {
  border-top: solid 1px #ffffff;
  height: 4.4rem;
  overflow: visible;
  transition: height 0.3s ease;
}

@media (min-width: 1921px) {
  .menu-item-has-children:hover ul li {
    height: 44px;
  }
}

.humberger {
  width: 4.8rem;
  height: 4.8rem;
  border-radius: 4px;
  background-color: #518C03;
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1001;
  cursor: pointer;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.26);
  margin-left: auto;
}

@media (max-width: 768px) {
  .humberger {
    display: flex;
  }
}

.humberger__line {
  width: 2.4rem;
